html {
  font-size: 10px;
  line-height: 14px;
  font-weight: 400;
  font-family: Roboto, sans-serif;
}

.body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  padding: 32px 16px 16px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 406px;
}

.card__image {
  display: block;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__name {
  margin: 0 0 4px;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
}

.card__rating {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
}

.card__rating-stars {
  display: flex;
}

.stars {
  height: 16px;
  width: 16px;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.stars:not(:last-child) {
  margin-right: 4px;
}

.stars:nth-child(-n+1),
.stars:nth-child(-n+2),
.stars:nth-child(-n+3),
.stars:nth-child(-n+4) {
  background-image: url(images/star-active.svg);
}

.card__price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.card__price-name {
  font-size: 12px;
  line-height: 18px;
  color: #616070;

}

.card__price-amount {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.card__buy {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 166px;
  height: 40px;
  background-color: #00acdc;
  border: 1px solid #00acdc ;
  border-radius: 5px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-decoration: none;
  color: #fff;
}

.card__buy:hover {
  background-color: #fff;
  color: #00acdc;
}
